import firebase from 'firebase';
import app from './firebase';
import axios from 'axios';

export const listAllContent = async (schoolid) => {
  try {
    let querySnapshot = null;
    if (schoolid) {
      querySnapshot = await app.firestore().collection('schools').doc(schoolid).collection('live_events').where('type','==','live_class').orderBy('created_at', 'desc').get();
    } else {
      querySnapshot = await app.firestore().collection('live_events').where('type','in',['live_class','school_live_class']).orderBy('created_at', 'desc').get();
    }
    const content = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      };
    });
    return content;
  } catch (err) {
    console.log(err);
  }
};

