export const addContent = async (values, image) => {

  try {
    let detail = ' <news content-type=\'news\'>' + values.detail + '</news>';
    delete values.detail;
    values.content_type = 'live_events';
    values.status = 0;
    if(typeof values.school.id!=='undefined') {
      values.type = 'school_live_class'
    }else{
      values.type = 'live_class';
    }
    values.created_at = firebase.firestore.FieldValue.serverTimestamp();
    let arr = values.tags;
    arr = arr.filter(item => item);
    values.tags = arr;
    console.log(values);
    let contentRef = await app.firestore().collection('live_events').add(values);

    let content = await contentRef.get();
    console.log('done content:' + content.id);
    contentRef.collection('detail').doc('content').set({ data: detail });
    let contentData = content.data();

    if (typeof (image) == 'undefined') {
      return;
    }
    var metadata = {
      contentType: 'image/jpeg'
    };
    let cover_url = 'public/images/content/' + content.id;
    var storageRef = app.storage().ref();
    var uploadTask = storageRef.child(cover_url).put(image, metadata);
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
      function(snapshot) {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload cover is ' + progress + '% done');
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED: // or 'paused'
            console.log('Upload cover is paused');
            break;
          case firebase.storage.TaskState.RUNNING: // or 'running'
            console.log('Upload cover is running');
            break;
        }
      },
      function(error) {

        switch (error.code) {
          case 'storage/unauthorized':
            console.log('unauthorized');
            break;
          case 'storage/canceled':
            console.log('cancel');
            break;
          case 'storage/unknown':

            console.log('unkonwn');
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },
      function() {
        // Upload completed successfully, now we can get the download URL
        uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          console.log('File available at', downloadURL);
          contentData.cover = downloadURL;
          app.firestore().collection('live_events').doc(content.id).update(contentData);
        });
      });
  } catch (err) {
    console.log(err);
  }
};

export const deleteContents = (ids) => {
  try {
    ids.map((id) => {
      let cover_url = 'public/images/content/' + id;
      var storageRef = app.storage().ref();
      storageRef.child(cover_url).getDownloadURL().then(onResolve, onReject);

      function onResolve(foundURL) {
        storageRef.child(cover_url).delete();
      }

      function onReject(error) {
        console.log(error.code);
      }

      app.firestore().collection('live_events').doc(id).collection('detail').doc('content').delete();
      app.firestore().collection('live_events').doc(id).delete();
    });
  } catch (err) {
    console.log(err);
  }
};


