import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Divider,
  IconButton,
  InputAdornment,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  Save as SaveIcon,
  Users as UsersIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon,
  Send as SendIcon
} from 'react-feather';
import getInitials from 'src/utils/getInitials';
import GetAppIcon from '@material-ui/icons/GetApp';
import { useHistory } from 'react-router-dom';
import {deleteEvents, exportEvent,autoSubmitEvent, exportEvent_Alldata} from '../../../../api/events.js';
import { useSnackbar } from 'notistack';
import {sendNotify} from '../../../../api/lives.js';
import { WaveLoading } from 'react-loadingg';
import { Alert } from '@material-ui/lab';
const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];

const sortOptions = [
  {
    value: 'updatedAt|desc',
    label: 'Last update (newest first)'
  },
  {
    value: 'updatedAt|asc',
    label: 'Last update (oldest first)'
  },
  {
    value: 'name|desc',
    label: 'Tên (giảm)'
  },
  {
    value: 'name|asc',
    label: 'Tên (tăng)'
  }
];

const applyFilters = (events, query, filters) => {
  return events.filter((event) => {
    let matches = true;

    if (query) {
      const properties = ['title', 'description'];
      let containsQuery = false;

      properties.forEach((property) => {
        if ((property in event) && event[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && event[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (events, page, limit) => {
  return events.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (events, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = events.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({
  className,
  events,
  ...rest
}) => {
  const history = useHistory();
  const classes = useStyles();
  const [openDialog, setOpenDialog] = useState(false);
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedEvents, setSelectedEvents] = useState([]);
  const { enqueueSnackbar } = useSnackbar();
  const handleClose = () => {
    setOpenDialog(false);
  };
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedEvents([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllEvents = (event) => {
    setSelectedEvents(event.target.checked
      ? events.map((event) => event.id)
      : []);
  };

  const handleSelectOneEvent = (event, eventId) => {
    if (!selectedEvents.includes(eventId)) {
      setSelectedEvents((prevSelected) => [...prevSelected, eventId]);
    } else {
      setSelectedEvents((prevSelected) => prevSelected.filter((id) => id !== eventId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const handleDelete = () => {
      deleteEvents(selectedEvents);
      history.push('/school/management/events');
    };

  const filteredEvents = applyFilters(events, query, filters);
  const sortedEvents = applySort(filteredEvents, sort);
  const paginatedEvents = applyPagination(sortedEvents, page, limit);
  const enableBulkOperations = selectedEvents.length > 0;
  const selectedSomeEvents = selectedEvents.length > 0 && selectedEvents.length < events.length;
  const selectedAllEvents = selectedEvents.length === events.length;

  const [isExportSuccess, setStatusExport] = useState(false);

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Tabs
        onChange={handleTabsChange}
        scrollButtons="auto"
        textColor="secondary"
        value={currentTab}
        variant="scrollable"
      >
        {isExportSuccess===true && (
          <WaveLoading />
        )}

        {tabs.map((tab) => (
          <Tab
            key={tab.value}
            value={tab.value}
            label={tab.label}
          />
        ))}
      </Tabs>
      <Divider />
      <Box
        p={2}
        minHeight={56}
        display="flex"
        alignItems="center"
      >
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon
                  fontSize="small"
                  color="action"
                >
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm"
          value={query}
          variant="outlined"
        />
        <Box flexGrow={1} />
        <TextField
          label="Sắp xếp"
          name="sort"
          onChange={handleSortChange}
          select
          SelectProps={{ native: true }}
          value={sort}
          variant="outlined"
        >
          {sortOptions.map((option) => (
            <option
              key={option.value}
              value={option.value}
            >
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox
              checked={selectedAllEvents}
              indeterminate={selectedSomeEvents}
              onChange={handleSelectAllEvents}
            />
            <Button
              variant="outlined"
              className={classes.bulkAction}
              onClick={handleDelete}
            >
              Xóa
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllEvents}
                    indeterminate={selectedSomeEvents}
                    onChange={handleSelectAllEvents}
                  />
                </TableCell>
                <TableCell>
                  Loại Event
                </TableCell>
                <TableCell>
                  Tiêu đề (name)
                </TableCell>
                <TableCell>
                  start time
                </TableCell>
                <TableCell>
                  end time
                </TableCell>
                <TableCell align="right">
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedEvents.map((event) => {
                let upvotes = null;
                let downvotes = null;
                if("reactions" in event){
                  upvotes = event.reactions.upvotes
                  downvotes = event.reactions.downvotes
                }

                const isEventSelected = selectedEvents.includes(event.id);

                return (
                  <TableRow
                    hover
                    key={event.id}
                    selected={isEventSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isEventSelected}
                        onChange={(event) => handleSelectOneEvent(event, event.id)}
                        value={isEventSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Box
                        display="flex"
                        alignItems="center"
                      >
                            {event.type}
                      </Box>
                    </TableCell>
                    <TableCell>
                            {event.title}
                    </TableCell>
                    <TableCell>
                            {event.start_at.toDate().toISOString()}
                    </TableCell>
                    <TableCell>
                            {event.end_at.toDate().toISOString()}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton title="Số lượng submit"
                          component={RouterLink}
                          to={"/school/management/events/submit/"+ event.id}
                      >
                        <SvgIcon fontSize="small">
                          <UsersIcon />
                        </SvgIcon>
                      </IconButton>
                      <IconButton title="Down load nhanh"
                        onClick={async() => {
                          setStatusExport(true);
                          await exportEvent(event.id);
                          setTimeout(function(){
                            setStatusExport(false);
                          },30000);
                        }}
                      >
                        <SvgIcon fontSize="small">
                          <SaveIcon />
                        </SvgIcon>
                      </IconButton>
                      <IconButton title="Down load tất cả"
                        onClick={async() => {
                          setStatusExport(true);
                          await exportEvent_Alldata(event.id);
                          setTimeout(function(){
                            setStatusExport(false);
                          },30000);
                        }}
                      >
                        <SvgIcon fontSize="small">
                          <GetAppIcon />
                        </SvgIcon>
                      </IconButton>
                      <IconButton
                        component={RouterLink}
                        to={"/school/management/events/edit/"+ event.id}
                      >
                        <SvgIcon fontSize="small">
                          <EditIcon />
                        </SvgIcon>
                      </IconButton>
                      <IconButton  onClick={() => {
                        setOpenDialog(event.id)
                      }}>
                        <SvgIcon fontSize="small">
                          <SendIcon />
                        </SvgIcon>
                      </IconButton>
                      <Dialog
                        open={openDialog==event.id}
                        onClose={handleClose}
                        aria-labelledby="draggable-dialog-title"
                        data={event.id}
                      >
                        <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                          Send noti
                        </DialogTitle>
                        <DialogContent>
                          <DialogContentText>
                           Bạn có chắc chắn muốn send noti cho  : {event.title} ?
                          </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                          <Button autoFocus onClick={handleClose} color="primary">
                            Cancel
                          </Button>
                          <Button
                            value={event.id} onClick={async () => {
                              let result = await sendNotify(event.id)
                              if(result.data.error_code==0){
                                enqueueSnackbar(result.data.message, {
                                  variant: 'success',
                                  action: <Button>See all</Button>
                                });

                              setOpenDialog(false);
                              } else {
                                alert(result.data.message)
                              }
                              history.push('/school/management/events');
                            }} color="primary">
                            Send
                          </Button>
                        </DialogActions>
                      </Dialog>
                      <IconButton  onClick={() => {
                        setOpenDialog(event.id)
                      }}>
                        <SvgIcon fontSize="small">
                          <SaveIcon />
                        </SvgIcon>
                      </IconButton>
                      <Dialog
                        open={openDialog==event.id}
                        onClose={handleClose}
                        aria-labelledby="draggable-dialog-title"
                        data={event.id}
                      >
                        <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                          Send noti
                        </DialogTitle>
                        <DialogContent>
                          <DialogContentText>
                           Bạn có chắc chắn muốn tự động nộp bài cho sự kiên  : {event.title} ?
                          </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                          <Button autoFocus onClick={handleClose} color="primary">
                            Cancel
                          </Button>
                          <Button
                            value={event.id} onClick={async () => {
                              let result = await autoSubmitEvent(event.id);
                              if(result.error_code==0){
                                enqueueSnackbar(result.message, {
                                  variant: 'success',
                                  action: <Button>See all</Button>
                                });

                              setOpenDialog(false);
                              } else {
                                alert(result.message)
                              }

                              history.push('/school/management/events');
                            }} color="primary">
                            Send
                          </Button>
                        </DialogActions>
                      </Dialog>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredEvents.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  events: PropTypes.array.isRequired
};

Results.defaultProps = {
  events: []
};

export default Results;
